//
// Panels
// --------------------------------------------------

.panel {
  border: none;
  .box-shadow(none);
  background: @gray-lightest; //#eee
}

.panel-heading {
  font-weight: bold;
}

// Within heading, strip any `h*` tag of its default margins for spacing.
.panel-title {
  font-size: @font-size-large;
  font-weight: bold;
}
