//
// Wells
// --------------------------------------------------

.well {
  .box-shadow(none);
  margin: 2px 0;

  p:last-child {
    margin-bottom: 0;
  }

  &.active {
    background: @gray-base;
    color: #fff;
  }
}

.well-backdrop {
  background: @well-backdrop-bg;
  display: block;
  padding: @well-backdrop-padding (@well-backdrop-padding * 2);
}