//
// Pagination (multiple pages)
// --------------------------------------------------

.pagination {

  &.primary {

    > .active > a,
    > .active > span {
      &,
      &:hover,
      &:focus {
        color: @pagination-primary-active-color;
        background-color: @pagination-primary-active-bg;
        border-color: @pagination-primary-active-border;
      }
    }
  }
}

.pager {
  a { text-decoration: none; }
}
