h1, .h1 {
  color: @gray-dark;
  font-family: @font-family-sans-serif-bold;
  vertical-align: middle;
  span { vertical-align: middle; }
}

h2, .h2 {
  color: @gray-dark;
  font-family: @font-family-sans-serif-bold;
  vertical-align: middle;
  span { vertical-align: middle; }
}

h3, .h3 {
  color: @gray-dark;
  font-family: @font-family-sans-serif;
  vertical-align: middle;
  span { vertical-align: middle; }
}


// Body text
// -------------------------

p {

}

//** Bold/strong text Over-ride
b, .b,
strong, .strong {
  font-family: @font-family-sans-serif-bold;
  font-weight: normal;
}

small,
.small {
  font-size: @small-font-ratio;
  vertical-align: middle;
}

u, .u,
.text-underline {
  text-decoration: underline;
}

a {
  text-decoration: underline;
  color: @gray-base;
}

//** Added Custom Font-weight(s) to Inline elements
.text-book { font-family: @font-family-sans-serif; }
.text-bold { font-family: @font-family-sans-serif-bold; }

//** Added vertical alignment classes for Inline elements
.text-valign-top { vertical-align: top; }
.text-valign-middle { vertical-align: middle; }
.text-valign-bottom { vertical-align: bottom; }


// Page header
// -------------------------

.page-header {
  padding-bottom: ((@line-height-computed / 2) - 1);
  margin: (@line-height-computed * 2) 0 @line-height-computed;
  border-bottom: 1px solid @page-header-border-color;
  text-align: center;
  vertical-align: middle;
}

// Section bi-line header
// -------------------------

.heading-byline {

  h2 {
    .font-size-to-em(18);
    color: @gray-dark;
    border-bottom: 3px solid #000;
    padding-bottom: @padding-base-vertical;
    margin-bottom: @padding-base-vertical;
    text-transform: uppercase;
  }

  h3 {
    .font-size-to-em(12);
    margin-top:  @padding-base-vertical;
    padding-left: @padding-base-vertical;
    color: @gray-base;
  }
}


// Emphasis & misc
// -------------------------

// Ex: (12px small font / 14px base font) * 100% = about 85%
mark,
.mark {
  background-color: lighten(@state-warning-border, 30%);
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary {
  // Given the contrast here, this is the only class to have its color inverted
  // automatically.
  color: #fff;
  .bg-variant(@brand-primary);
}
.bg-gray {
  .bg-variant(@gray-lightest);
}
.bg-success {
  .bg-variant(lighten(@state-success-border, 35%));
}
.bg-info {
  .bg-variant(lighten(@state-info-border, 15%));
}
.bg-warning {
  .bg-variant(lighten(@state-warning-border, 30%));
}
.bg-danger {
  .bg-variant(lighten(@state-danger-border, 40%));
}

/** Code over-write for documentation
  * Putting it here since making a new _custom stylesheet
  * was over-kill
  */
pre {
  font-size: .8em !important;
}
