//
// Unpadded grid
// --------------------------------------------------


// Grid container
//
// Grid cols are generated within removing padding from left and right

//== Unpadded Grid Column Generator
//
//## Create new grid with padding removed
.make-unpadded-grid-columns() {
  // Common styles for all sizes of grid columns, widths 1-12
  .col(@index) { // initial
    @item: ~".unpadded-grid .col-xs-@{index}, .unpadded-grid .col-sm-@{index}, .unpadded-grid .col-md-@{index}, .unpadded-grid .col-lg-@{index}, .unpadded-grid .col-xs-@{index} .unpadded-grid .col-xs-@{index}, .unpadded-grid .col-sm-@{index} .unpadded-grid .col-sm-@{index}, .unpadded-grid .col-md-@{index} .unpadded-grid .col-md-@{index}, .unpadded-grid .col-lg-@{index} .unpadded-grid .col-lg-@{index}";
    .col((@index + 1), @item);
  }
  .col(@index, @list) when (@index =< @grid-columns) { // general; "=<" isn't a typo
    @item: ~".unpadded-grid .col-xs-@{index}, .unpadded-grid .col-sm-@{index}, .unpadded-grid .col-md-@{index}, .unpadded-grid .col-lg-@{index}, .unpadded-grid .col-xs-@{index} .unpadded-grid .col-xs-@{index}, .unpadded-grid .col-sm-@{index} .unpadded-grid .col-sm-@{index}, .unpadded-grid .col-md-@{index} .unpadded-grid .col-md-@{index}, .unpadded-grid .col-lg-@{index} .unpadded-grid .col-lg-@{index}";
    .col((@index + 1), ~"@{list}, @{item}");
  }
  .col(@index, @list) when (@index > @grid-columns) { // terminal
    @{list} {
      // Inner gutter via padding
      padding-left:  0;
      padding-right: 0;
    }
  }
  .col(1); // kickstart it
}

.make-nested-grid-columns() {
  // Common styles for all sizes of grid columns, widths 1-12
  .col(@index) { // initial
    @item: ~".nested-grid .col-xs-@{index}, .nested-grid .col-sm-@{index}, .nested-grid .col-md-@{index}, .nested-grid .col-lg-@{index}";
    .col((@index + 1), @item);
  }
  .col(@index, @list) when (@index =< @grid-columns) { // general; "=<" isn't a typo
    @item: ~".nested-grid .col-xs-@{index}, .nested-grid .col-sm-@{index}, .nested-grid .col-md-@{index}, .nested-grid .col-lg-@{index}";
    .col((@index + 1), ~"@{list}, @{item}");
  }
  .col(@index, @list) when (@index > @grid-columns) { // terminal
    @{list} {
      // Inner gutter via padding
      padding-left:  (@grid-gutter-width / 2);
      padding-right: (@grid-gutter-width / 2);
    }
  }
  .col(1); // kickstart it
}

//** Fire off Mixins to create unpadded Grid with nested Grids
.make-unpadded-grid-columns;
.make-nested-grid-columns;

//** Unpadded Grid row Over-ride
//
//## remove negative margin from 'row' that uses the unpadded grid
//   with the unpadded grid, 'row' is used more for code organization
//   or changes to the grid in different break points.
.unpadded-grid {

  &.row {
    margin-left: 0;
    margin-right: 0;
  }

}
