@font-face {
  font-family: 'aeoicons';
  src: ~"url('@{icon-font-path}@{aeoicon-font-name}.eot')";
  src: ~"url('@{icon-font-path}@{aeoicon-font-name}.eot?#iefix') format('embedded-opentype')",
       ~"url('@{icon-font-path}@{aeoicon-font-name}.woff') format('woff')",
       ~"url('@{icon-font-path}@{aeoicon-font-name}.ttf') format('truetype')",
       ~"url('@{icon-font-path}@{aeoicon-font-name}.svg#@{aeoicon-font-svg-id}') format('svg')";
}

.aeoicon {
  font-family: 'aeoicons';
  speak: none;
  text-decoration: none;
  /* This is non-standard; it should not be used; it does look nice*/
  //** -webkit-font-smoothing: subpixel-antialiased;
  position: relative;
  top: 1px;
  display: inline-block;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

