//== Custom Mixins and Over-riding Existing Mixins
//
//## Define custom mixins and modify existing mixins.


//== Form Mixins
//
//## Over-ride core form mixins.

//** Form field focus styling
.form-control-focus(@color: #000) {
  .transition();
  outline: none;
  .box-shadow(none);
  -webkit-backface-visibility: hidden;

  &:focus {
    .focus-box-shadow(@color);
  }
}

.focus-box-shadow (@color: #000){
  outline: none;
  border-color: @color;
  .box-shadow(inset 0px 0px 0px 1px @color);
  .transition();
  -webkit-backface-visibility: hidden;
}

//** Custom Transition Animation
.transition(@type: all, @time: .5s){
  transition: @type @time;
  -moz-transition: @type @time;
  -webkit-transition: @type @time;
  -o-transition: @type @time;
}

.font-size-to-em(@size: 16){
  font-size: unit((@size / @font-base), em);
}
