//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
  .font-size-to-em(18);
  text-transform: uppercase;
  font-family: @font-family-sans-serif-bold;
  border-radius: 0;
  text-decoration: none;
  cursor: pointer;
  border-width: 1px;

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      //outline-color: @gray-lighter;
      outline: none;
    }
  }

  &:hover,
  &:focus {
    .box-shadow(none);
    outline: none;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: not-allowed;
    pointer-events: none; // Future-proof disabling of clicks
    .opacity(1);
    .box-shadow(none);
  }

  &.btn-lg {
    font-size: unit(((@font-size-large * .5) + @font-size-large), em);
  }
  &.btn-sm {
    // line-height: ensure proper height of button next to small input
    .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-base; @line-height-small; @border-radius-small);
  }
  &.btn-xs {
    .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @border-radius-small);
  }
}

.btn-primary,
.btn-default {
  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
  .focus-box-shadow(@btn-primary-border);

  &:hover,
  &:focus {
    background: @btn-primary-hover;
    .focus-box-shadow(@btn-primary-border);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .button-variant(lighten(@btn-primary-color, 40%), @btn-primary-bg, lighten(@btn-primary-border, 40%));
  }
}

.btn-secondary {
  .button-variant(@btn-secondary-color; @btn-secondary-bg; @btn-secondary-border);
  .focus-box-shadow(@btn-secondary-border);

  &:hover,
  &:focus {
    background: @btn-secondary-hover;
    .focus-box-shadow(@btn-secondary-hover);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .button-variant(desaturate(lighten(@btn-secondary-color, 40%), 60%); desaturate(lighten(@btn-secondary-bg, 40%), 60%); desaturate(lighten(@btn-secondary-border, 40%), 60%));
