//
// Forms
// --------------------------------------------------


// Normalize non-controls
//
// Restyle and baseline non-control form elements.

.form-group,
.checkbox {
  position: relative;
  z-index: 1;
}

//** Custom Form Styles
.form-control {
  color: @form-text;
  font-family: @font-family-sans-serif;
  .font-size-to-em(13);
  position: relative;
  height: auto;
  padding: @input-padding-vertical @input-padding-horizontal (@input-padding-vertical + 1px);
  z-index: 1;

  // Box Shadow
  .box-shadow(none);

  // Placeholder
  .placeholder(@form-placeholder);

  // Focus
  .form-control-focus();
}


fieldset[disabled] .caret { color: @input-color-disabled !important; }

.has-success {
  .form-control { .focus-box-shadow(@brand-success); }
  .input-group-addon { border-width: 2px; }
}
.has-warning {
  .form-control { .focus-box-shadow(@brand-warning); }
  .input-group-addon { border-width: 2px; }
}
.has-error {
  .form-control { .focus-box-shadow(@brand-danger); }
  .input-group-addon { border-width: 2px; }
}

//** Custom CSS-only Checkboxes

/* Base for label styling */
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
}
[type="checkbox"]:not(:checked) ~ label,
[type="checkbox"]:checked ~ label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  line-height: 20px;
  height: 20px;
}
[type="checkbox"]:not(:checked) ~ label {
  font-family: @font-family-sans-serif;
}
[type="checkbox"]:checked ~ label {
  font-family: @font-family-sans-serif-bold;
}
/* checkbox aspect */
[type="checkbox"]:not(:checked) ~ label:before,
[type="checkbox"]:checked ~ label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background:#fff;
  border: 1px solid @gray-base;
}
/* checked mark aspect */
[type="checkbox"]:not(:checked) ~ label:after,
[type="checkbox"]:checked ~ label:after   {
  content: '\2714';
  font-family: 'aeoicons';
  position: absolute;
  top: 1px;
  left: 2px;
  font-size: @font-size-base;
  transition: all .2s;
}
/* checked mark aspect changes */
[type="checkbox"]:not(:checked) ~ label:after {
  opacity: 0;
  transform: scale(0);
}
[type="checkbox"]:checked ~ label:after {
  opacity: 1;
  transform: scale(1);
}
/* disabled checkbox */
[type="checkbox"]:disabled:not(:checked) ~ label:before,
[type="checkbox"]:disabled:checked ~ label:before {
  box-shadow: none;
  border-color: @input-border-disabled;
  background-color: #fff;
  cursor: not-allowed;
}
[type="checkbox"]:disabled:checked ~ label:after  {
  color: @input-color-disabled;
  cursor: not-allowed;
}
[type="checkbox"]:disabled ~ label {
  color: @input-color-disabled;
  cursor: not-allowed;
}
/* accessibility */
[type="checkbox"]:checked:focus ~ label:before,
[type="checkbox"]:not(:checked):focus ~ label:before  {
  .form-control-focus();
}

//** Custom CSS-only Radio Buttons

/* Base for label styling */
[type="radio"]:not(:checked),
[type="radio"]:checked {
  position: absolute;
  left: -9999px;
}
[type="radio"]:not(:checked) + label,
[type="radio"]:checked + label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  line-height: 20px;
  height: 20px;
  font-family: @font-family-sans-serif;
}
[type="radio"]:not(:checked) ~ label {
  font-family: @font-family-sans-serif;
}
[type="radio"]:checked ~ label {
  font-family: @font-family-sans-serif-bold;
}
[type="radio"]:not(:checked) + label.simple,
[type="radio"]:checked + label.simple {
  position: relative;
  cursor: pointer;
  height: auto;
  color: #fff;
  background: @gray-base;
  padding: @padding-base-vertical @padding-base-horizontal;
  font-size: inherit;
  font-family: inherit;
  line-height: normal;
}
[type="radio"]:not(:checked) + label.simple {
  background: #fff;
  border-color: @gray-base;
  color: @gray-base;
}

/* checkbox aspect */
[type="radio"]:not(:checked) + label:before,
[type="radio"]:checked + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 20px;
  border: 1px solid @gray-base;
}
[type="radio"]:not(:checked) + label.simple:before,
[type="radio"]:checked + label.simple:before {
  position: relative;
  left: auto;
  top: auto;
  width: auto;
  height: auto;
  background: none;
  border-radius: 0;
  border: none;
  font-size: inherit;
  font-family: inherit;
  line-height: normal;
}
/* checked mark aspect */
[type="radio"]:not(:checked) + label:after,
[type="radio"]:checked + label:after {
  background: @gray-base;
  border-radius: 10px;
  width: 10px;
  height: 10px;
  content: ' ';
  position: absolute;
  top: 5px;
  left: 5px;
  transition: all .2s;
}
[type="radio"]:not(:checked) + label.simple:after,
[type="radio"]:checked + label.simple:after {
  background: none;
  border-radius: 0;
  width: auto;
  height: auto;
  position: relative;
  top: auto;
  left: auto;
  transition: all .2s;
}
/* checked mark aspect changes */
[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0);
}
[type="radio"]:checked + label:after {
  opacity: 1;
  transform: scale(1);
}
/* disabled checkbox */
[type="radio"]:disabled:not(:checked) + label:before,
[type="radio"]:disabled:checked + label:before {
  box-shadow: none;
  border-color: @input-border-disabled;
  background-color: #fff;
  cursor: not-allowed;
}
[type="radio"]:disabled:checked + label:after {
  color: @input-color-disabled;
  cursor: not-allowed;
}
[type="radio"]:disabled + label {
  color: @input-color-disabled;
  cursor: not-allowed;
}
/* accessibility */
[type="radio"]:checked:focus + label:before,
[type="radio"]:not(:checked):focus + label:before {
  .form-control-focus();
}


//** Custom Checkbox Error Styling
// Feedback states
.has-success {
  .checkbox {
    label:before {
      border-color: @brand-success;
    }
  }
}
.has-warning {
  .checkbox {
    label:before {
      border-color: @brand-warning;
    }
  }
}
.has-error {
  .checkbox {
    label:before {
      border-color: @brand-danger;
    }
  }
}

//** Inline Checkboxes and Inline Radio Button Over-rides
.radio-inline,
.checkbox-inline {
  padding-left: 0;
  margin-top: 0;
}

//** AEO Input Component Styles
.aeo-input {
  position: relative;

  label {
    position: absolute;
    padding: 0 @padding-base-horizontal;
    height: @input-height-base;
    line-height: @line-height-base;
    color: @gray;
    .opacity(0);
    .transition('top, color, font-size, opacity', 400ms);
  }

  input, textarea {
    z-index: 1;
    position: relative;
    color: @gray-base;
    border-color: @gray;
    padding: @input-padding-vertical @input-padding-horizontal (@input-padding-vertical + 1px);

    &::-webkit-input-placeholder {
      .opacity(1);
      .transition(opacity, 400ms);
    }
  }

  placeholder {
    color: @gray;
  }

  &.not-empty {

    input, textarea {
      color: @gray-base;
      border-color: @gray-base;
      padding: (@input-padding-vertical + 9px) @input-padding-horizontal (@input-padding-vertical - 8px);
      .focus-box-shadow(@gray-base);

      &::-webkit-input-placeholder {
        .opacity(0);
      }
    }

    label {
      top: 9px;
      left: 1px;
      right: 1px;
      height: auto;
      color: @gray;
      font-family: @font-family-sans-serif;
      font-weight: normal;
      z-index: 3;
      font-size: 62.5%;
      overflow:hidden;
      white-space: nowrap;
      .opacity(1);
    }

    .form-control-feedback {
      top: 0;
      z-index: 3;
      font-size: ((@font-size-base * .5) + @font-size-base);
    }
  }

  &.is-disabled,
  &.is-readonly {
    textarea, input {
      color: @input-color-disabled;
      background: @input-bg-disabled;
      border-color: @input-border-disabled;
    }

    &.not-empty {
      textarea, input {
        .focus-box-shadow(@input-border-disabled);
        border-color: @input-border-disabled;
      }
    }

    label {
      color: @input-color-disabled;
    }
  }

  /**
   * Jira AWC-583
   * The autocomplete feature of Webkit (Chrome, Safari)
   * places a yellow background on input fields.
   * This clashes with the white background of the label that overlays the input field.
   * Webkit doesn't allow changing the background property.
   * The trick uses an inset shadow to make the background look white.
   */
  // input:-webkit-autofill,
  // input:-webkit-autofill:hover,
  // input:-webkit-autofill:active,
  // input:-webkit-autofill:focus {
  //   -webkit-box-shadow: 0 0 0 1000px white inset;
  // }

}

//** aeo-input Validation States
.has-success {
  input,
  textarea,
  .form-control {
    border-color: @brand-success;
    .form-control-focus(@brand-success);
    .focus-box-shadow(@brand-success);
  }
}
.has-warning {
  input,
  textarea,
  .form-control  {
    border-color: @brand-warning;
    .form-control-focus(@brand-warning);
    .focus-box-shadow(@brand-warning);
  }
}
.has-error {
  input,
  textarea,
  .form-control  {
    border-color: @brand-danger;
    .form-control-focus(@brand-danger);
    .focus-box-shadow(@brand-danger);
  }
}


//** Custom CSS Select Boxes

.select-control {
  position: relative;
  // .font-size-to-em(13);

  label {
    .font-size-to-em(16);
    color: @gray;
    line-height: 1.5;
    padding: @input-padding-vertical @input-padding-horizontal (@input-padding-vertical + 1px);
    font-weight: normal;
    position: absolute;
    top: 0;
    margin-bottom: 0;
    background: none;
    .font-size-to-em(13);
    .transition;
  }

  .form-control {
    background: transparent;
    border-radius: 0;
    border: 1px solid @gray;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: '';
    color: transparent;
    height: auto;
    position: relative;
    z-index: 2;
    .font-size-to-em(13);

    &:focus {
      border-color: @gray-base;
    }
  }

  &.has-selection {
    label {
      position: absolute;
      padding-top: 9px;
      padding-left: @input-padding-horizontal;
      padding-right: @input-padding-horizontal;
      height: auto;
      top: 0;
      z-index: 1;
      line-height: @line-height-base;
      color: @gray;
      font-size: 62.5%;
      .transition;
    }

    .form-control {
      color: @gray-base;
      border-color: @gray-base;
      .font-size-to-em(13);
      .focus-box-shadow(@gray-base);
      padding: (@input-padding-vertical + 9px) @input-padding-horizontal (@input-padding-vertical - 8px);
    }

    &.is-disabled,
    &.is-readonly {
      .form-control {
        border-color: @input-border-disabled;
        .focus-box-shadow(@input-border-disabled);
      }
    }
  }

  .caret {
    position: absolute;
    z-index: 2;
    right: 2.5%;
    top: 0;
    font-size: (@font-size-base * 1.5);
    line-height: (@line-height-base * 1.5);
  }

  .form-control-feedback {
    right: 6%;
    font-size: ((@font-size-base * .5) + @font-size-base);
    line-height: (@line-height-base * 1.6);
  }

  &.has-success {
    .caret { color: @brand-success; }

    .form-control {
      border-color: @brand-success;
      .form-control-focus(@brand-success);
      .focus-box-shadow(@brand-success);
    }

    &.has-selection {
      color: @gray-base;
      label {
        color: @gray;
      }
    }
  }
  &.has-warning {
    .caret { color: @brand-warning; }

    .form-control {
      border-color: @brand-warning;
      .form-control-focus(@brand-warning);
      .focus-box-shadow(@brand-warning);
    }

    &.has-selection {
      color: @gray-base;
      label {
        color: @gray;
      }
    }
  }
  &.has-error {
    .caret { color: @brand-danger; }

    .form-control {
      border-color: @brand-danger;
      .form-control-focus(@brand-danger);
      .focus-box-shadow(@brand-danger);
    }

    &.has-selection {
      color: @gray-base;
      label {
        color: @gray;
      }
    }
  }

  &.is-disabled,
  &.is-readonly,
  &.is-disabled.has-selection,
  &.is-readonly.has-selection {
    background: @input-bg-disabled;
    border-color: @input-border-disabled;

    label {
      z-index: 3 !important;
      color: @input-color-disabled;
    }

    .caret {
      color: @input-color-disabled;
    }
  }

  &.is-disabled,
  &.is-readonly {
    color: @input-color-disabled;
  }
}

.help-block {
  color: @gray-base;
  font-size: unit((11 / @font-base), em);
  margin-top: 5px;
  margin-bottom: 20px;

  &.has-error {
    color: @brand-danger;
  }
}

