//** Sizing Over-rides
.aeo-input,
.select-control {
  padding: 0;
  position: relative;

  &.input-lg {
    height: auto;

    label,
    .form-control {
      font-size: @font-size-large;
      padding: (@padding-large-vertical + 3px) @padding-large-horizontal (@padding-large-vertical + 4px);
    }

    &.has-selection,
    &.not-empty {
      .form-control {
        padding-top: 30px;
        padding-bottom: 11px;
      }

      label {
        top: 0;
        font-size: 62.5%;
        padding: (@select-padding-large-horizontal + 3px) @padding-large-horizontal 0;
        line-height: @line-height-large;
      }
    }

    .caret, .form-control-feedback { font-size: (@font-size-large * 1.5); line-height: (@line-height-large * 1.8); }
  }

  &.input-sm {
    height: auto;

    label,
    .form-control {
      padding: (@padding-small-vertical + 3px) @padding-small-horizontal (@padding-small-vertical + 2px);
      line-height: @line-height-small;
      font-size: unit(((@font-size-small * .4) + @font-size-small), em);
    }

    &.has-selection,
    &.not-empty {
      label {
        top: 6px;
        padding-top: 0;
        padding-bottom: 0;
        font-size: 62.5%;
      }

      .form-control {
        padding: 15px @padding-small-horizontal 4px;
      }
    }
    .caret, .form-control-feedback { font-size: @font-size-small; line-height: (@line-height-small * 3); }
  }
}

// Moved Disabled styling to bottom to over-write aeo-input/select-control base styling
.form-control {
  // Disabled and read-only inputs
  //
  // HTML5 says that controls under a fieldset > legend:first-child won't be
  // disabled if the fieldset is disabled. Due to implementation difficulty, we
  // don't honor that edge case; we style them as disabled anyway.
  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    color: @input-color-disabled;
    cursor: not-allowed;
    background-color: @input-bg-disabled;
    border-color: @input-border-disabled;
    .placeholder(@input-color-disabled);
  }

  &[disabled] ~ label,
  fieldset[disabled] & ~ label {
    color: @input-color-disabled;
    z-index: 3;
  }

  &.is-disabled,
  &.is-readonly {
    label,
    .form-control {
      color: @input-color-disabled;
    }
  }
}

.aeo-input {

  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    label,
    .form-control {
      color: @input-color-disabled;
      border-color: @input-border-disabled
    }
  }

  &.not-empty {

    .form-control {
      &[disabled],
      &[readonly],
      fieldset[disabled] & {
        .focus-box-shadow(@input-border-disabled);
        border-color: @input-border-disabled;
        color: @input-color-disabled;
      }
    }
  }

  &.is-disabled,
  &.is-readonly {
    label,
    .form-control {
      color: @input-color-disabled;
    }
  }
}

.select-control {

  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    .form-control {
      color: @input-border-disabled;
    }
  }

  .form-control {
    &[disabled],
    &[readonly],
    fieldset[disabled] & {
      color: transparent;
      border-color: @input-border-disabled;
    }
  }

  &.has-selection {
    .form-control {
      &[disabled],
      &[readonly],
      fieldset[disabled] & {
        border-color: @input-border-disabled;
        .focus-box-shadow(@input-border-disabled);
        color: @input-color-disabled;
      }
    }
  }

  &.is-disabled,
  &.is-readonly {
    &.has-selection {
      label,
      .form-control {
        color: @input-color-disabled;
      }
    }
  }
}


//** Horizontal Form Groups
.form-horizontal .form-group {
  margin-left: 0;
  margin-right: 0;
}


//** Btn-group over-rides
.btn-group {

  &.open {
    .dropdown-toggle {

      &.btn-brand {
        border-color: @btn-brand-hover;
        background: @btn-brand-hover;
        .focus-box-shadow(@btn-brand-hover);
      }
      &.btn-success {
        border-color: @btn-success-hover;
        background: @btn-success-hover;
        .focus-box-shadow(@btn-success-hover);
      }
      &.btn-info {
        border-color: @btn-info-hover;
        background: @btn-info-hover;
        .focus-box-shadow(@btn-info-hover);
      }
      &.btn-warning {
        border-color: @btn-warning-hover;
        background: @btn-warning-hover;
        .focus-box-shadow(@btn-warning-hover);
      }
      &.btn-danger {
        border-color: @btn-danger-hover;
        background: @btn-danger-hover;
        .focus-box-shadow(@btn-danger-hover);
      }
    }
  }
}
