//
// Custom Extensions of Bootstrap
// --------------------------------------------------

//** Custom Text Selection Colors
::selection {
  background: @gray-base; /* WebKit/Blink Browsers */
  color:#fff;
}
::-moz-selection {
  background: @gray-base; /* Gecko Browsers */
  color:#fff;
}

.bg-facebook {
  .bg-variant(@social-facebook);
  color: #fff;
}
.bg-twitter {
  .bg-variant(@social-twitter);
  color: #fff;
}
.bg-pinterest {
  .bg-variant(@social-pinterest);
  color: #fff;
}
.bg-shopkick {
  .bg-variant(@social-shopkick);
  color: #fff;
}
