//== Alerts
//
//## Define custom styles for alerts.

.alert {
   // Provide class for links that match alerts
  margin-bottom: 15px;
  
  .alert-link {
    font-weight: normal;
    text-decoration: underline;
  }

  //** Custom alert with icon
  
  &.alert-with-icon {
    display: table;
    width: 100%;
    padding: 0;

    .alert-icon {
      display: table-cell;
      vertical-align: middle;
      font-size: 3em;
      padding: 10px;
    }

    .alert-message {
      display: table-cell;
      vertical-align: middle;
      width: 95%;
      padding: 10px;
    }
  }
}