//
// Button groups
// --------------------------------------------------

.btn-group {

  .btn-default,
  .btn-primary {
    background: #fff;
    border-width: 1px;
    border-color: @btn-primary-border;
    color: @gray-base;
    .focus-box-shadow(@btn-primary-border);

    &,
    &:active,
    &.active {
      &:focus,
      &.focus {
        background: lighten(@gray-base, 20%);
        border-color: lighten(@gray-base, 20%);
        .focus-box-shadow(@btn-primary-border);
        color: #fff;
      }
    }
  }

  //** Over-ride Open Toggle Styles
  &.open {
    .dropdown-toggle {
      .focus-box-shadow(@btn-primary-border);
    }

    .btn-default,
    .btn-primary {

      &,
      &:active,
      &.active {
        &:focus,
        &.focus {
          background: @gray-base;
        }
      }
    }
  }
}

// Prevent double borders when buttons are next to each other
.btn-group {
  .btn + .btn,
  .btn + .btn-group,
  .btn-group + .btn,
  .btn-group + .btn-group {
    margin-left: -2px;
  }
}

// Vertical button groups
// ----------------------

.btn-group-vertical {

  > .btn + .btn,
  > .btn + .btn-group,
  > .btn-group + .btn,
  > .btn-group + .btn-group {
    margin-top: -2px;
    margin-left: 0;
  }
}
