//== Gotham Medium, Gotham Book fonts
//
//## Any 'bold' text uses Gotham Medium

//** Gotham Medium import
@font-face {
  font-family: 'Gotham Medium';
  src: url('@{icon-font-path}DFC40F093C219AAEB.eot'),
    url("@{icon-font-path}DFC40F093C219AAEB.eot?#hco") format('embedded-opentype'),
    url("data:application/x-font-woff;base64,"),url("data:font/truetype;base64,");
    font-weight: normal;
    font-style: normal;
}

//** Gotham Book import
@font-face {
  font-family: 'Gotham Book';
  src: url("@{icon-font-path}3452D1D72F9A741BD.eot"),
    url("@{icon-font-path}3452D1D72F9A741BD.eot?#hco") format('embedded-opentype'),
    url("data:application/x-font-woff;base64,"),url("data:font/truetype;base64,");
    font-weight: normal;
    font-style: normal;
}

