//** Custom Extensions that may be useful

.xs-pull-right {
  float: right !important;
}
.xs-pull-left {
  float: left !important;
}

@media (min-width: @screen-sm-min) {
  .sm-pull-right {
    float: right !important;
  }
  .sm-pull-left {
    float: left !important;
  }
}

@media (min-width: @screen-md-min) {
  .md-pull-right {
    float: right !important;
  }
  .md-pull-left {
    float: left !important;
  }
}

@media (min-width: @screen-lg-min) {
  .lg-pull-right {
    float: right !important;
  }
  .lg-pull-left {
    float: left !important;
  }
}
