//
// Navs
// --------------------------------------------------


// Base class
// --------------------------------------------------

.nav {
  
  > li {

    > a {
      color: @nav-link-color;
      text-decoration: none;
    }
  }

  // Open dropdowns
  .open > a {
    &,
    &:hover,
    &:focus {
      background-color: @gray-base;
      border-color: @gray-base;
      color:#fff;
      .opacity(.75);
    }
  }
}

.nav-stacked ul > li > a {
  padding-left: (@padding-large-horizontal * 2);
  font-size: @font-size-small;
  cursor: pointer;
}

.nav-right {
  width: 100%;
  .clearfix();

  .nav {
    float: right;
    width: auto;
  }

  .nav-tabs {
    border-bottom: none;
  }
  // The below class restores the bottom-border on .nav-tabs for right-aligned navs
  &.has-tabs {
    border-bottom: 1px solid @nav-tabs-border-color;
  }
}
